function createData(condition, date, number, part, station, equipnum, equipcate, equipname, locate, iot, flaw) {
  let rowclass1;
  if(condition == '정지중') {
    rowclass1='condition1';
  } else if(condition == '운용중') {
    rowclass1='condition6';
  } else {
    rowclass1=''
  }

  let rowclass2;
  let equipdetail;

  if(flaw == '불량') {
    rowclass2='condition1';
    equipdetail='/equipdetail1';
  } else if(flaw == '결함') {
    rowclass2='condition2';
    equipdetail='/equipdetail1';
  } else if(flaw == '미흡') {
    rowclass2='condition3';
    equipdetail='/equipdetail1';
  } else if(flaw == '통신') {
    rowclass2='condition4';
    equipdetail='/equipdetail1';
  } else if(flaw == '양호') {
    rowclass2='condition5';
    equipdetail='/equiplist';
  } else if(flaw == '보통') {
    rowclass2='condition6';
    equipdetail='/equiplist';
  } else {
    rowclass2=''
  }

  return { condition, date, number, part, station, equipnum, equipcate, equipname, locate, iot, flaw, rowclass1, rowclass2, equipdetail };
}

