// table data
function createData1(number, part, station, equipname, alertype, division, check) {
  let rowclass;
  let spotdetail;

  if(check == '경보발생') {
    rowclass='condition1';
    spotdetail='/spotdetail1';
  } else if(check == '경보확인') {
    rowclass='condition2';
  } else if(check == '현장점검') {
    rowclass='condition4';
    spotdetail='/spotdetail2';
  } else {
    rowclass=''
  }
  return { number, part, station, equipname, alertype, division, check, rowclass, spotdetail };
}

