function createData1(data1, data2, data3, data4, data5, data6, data7, data8) {
  let rowclass;
  if(data8 == '대기') {
    rowclass='condition1';
  } else if(data8 == '작업중') {
    rowclass='condition3';
  } else if(data8 == '종료') {
    rowclass='condition5';
  } else if(data8 == '확인') {
    rowclass='condition6';
  } else {
    rowclass=''
  }
  return { data1, data2, data3, data4, data5, data6, data7, data8, rowclass };
}

