import React from "react";
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

import MroSetup11 from "./MroSetup11";
import MroSetup12 from "./MroSetup12";
import MroSetup13 from "./MroSetup13";
import MroSetup14 from "./MroSetup14";
import MroSetup15 from "./MroSetup15";
import MroSetup16 from "./MroSetup16";
import MroSetup17 from "./MroSetup17";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function MroSetupMenu() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%' }} className="mrosetup_tabs">
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="설비관리" {...a11yProps(0)} />
          <Tab label="설비분류관리" {...a11yProps(1)} />
          <Tab label="설비현황 기준관리" {...a11yProps(2)} />
          <Tab label="유지관리 점검 항목 관리" {...a11yProps(3)} />
          <Tab label="성능 점검 항목 관리" {...a11yProps(4)} />
          <Tab label="자재관리" {...a11yProps(5)} />
          <Tab label="공구관리" {...a11yProps(6)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <MroSetup11/>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <MroSetup12/>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <MroSetup13/>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <MroSetup14/>
      </TabPanel>
      <TabPanel value={value} index={4}>
        <MroSetup15/>
      </TabPanel>
      <TabPanel value={value} index={5}>
        <MroSetup16/>
      </TabPanel>
      <TabPanel value={value} index={6}>
        <MroSetup17/>
      </TabPanel>
    </Box>
  )
}

export default MroSetupMenu;