import Alert from 'custom/components/pop/alert';

function rowClass1(condition) {
  if(condition == '불량') {
    return 'icon icon1';
  } else if(condition == '결함') {
    return 'icon icon2';
  } else if(condition == '미흡') {
    return 'icon icon3';
  } else if(condition == '통신') {
    return 'icon icon4';
  } else if(condition == '보통') {
    return 'icon icon5';
  } else if(condition == '양호') {
    return 'icon icon6';
  } else {
    return ''
  }
}

function rowClass2(circleclass) {
  if(circleclass == 'A') {
    return 'circle1';
  } else if(circleclass == 'B') {
    return 'circle2';
  } else if(circleclass == 'C') {
    return 'circle3';
  } else if(circleclass == 'D') {
    return 'circle4';
  } else {
    return ''
  }
}

const columns = [
  { id: 'equipname', label: '설비명', minWidth: 150 },
  { id: 'equipnum', label: '설비번호', minWidth: 130 },
  { id: 'equipstate', label: '설비상태진단', minWidth: 150 },
  { id: 'condition', label: '설비결함진단(고장예측)', minWidth: 170 },
  { id: 'code', label: '고장신고/점검요청', minWidth: 100 }
];

// table data
function createData1(equipname, equinum, condition1, x1, y1, z1, condition2, x2, y2, z2, condition3, condition4 ) {
  let rowclass1 = rowClass1(condition1);
  let rowclass2 = rowClass1(condition2);
  let rowclass3 = rowClass1(condition3);
  let rowclass4 = rowClass1(condition4);

  let circleclass1 = rowClass2(x1);
  let circleclass2 = rowClass2(y1);
  let circleclass3 = rowClass2(z1);
  let circleclass4 = rowClass2(x2);
  let circleclass5 = rowClass2(y2);
  let circleclass6 = rowClass2(z2);

  return { equipname, equinum, condition1, x1, y1, z1, condition2, x2, y2, z2, condition3, condition4, rowclass1, rowclass2, rowclass3, rowclass4, circleclass1, circleclass2, circleclass3, circleclass4, circleclass5, circleclass6};
}

